.App {
  text-align: center;
}

.header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 1vw 1vw 1vw 1vw;
  border-bottom: 3px solid #08acd5;
}

 .navBar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  list-style-type: none;
  -ms-flex-direction: column;
      flex-direction: column;
}

.navBar a {
  font-family: 'Crete Round', serif;
  text-decoration: none;
  color: #292929;
  font-size: 2em;
  width: 100px;
}



.navBar li {
  margin-top: 15%;
}

.navBar .active{
  width: 100px;
  color: #ee1800;
}

.mobile-menu{
  padding-top: .6em;
  font-size: 30px;
}

 .mobile-nav {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    width: 100%;
    -ms-flex-pack: distribute;
        justify-content: space-around;
  }

  .slideBar{
    padding-top: 10%;
    padding-right: 10%;
    padding-left: 10%;
    border-right: 1.2vw solid #292929;
    display: -ms-flexbox;
    display: flex;
    left: 0;
    top: 0;
    position: absolute;
    height: 100%;
    background-color: white;
  }

   .slide {
    position: fixed;
    width: 0;
    overflow-x: hidden;
  } 

  .nav {
    margin-top: 15%;
  }

  .show{
    display: inline-block;
    width: 40%;
    height: 330px;
    margin: 0 2.5%;
    margin-bottom: .5%;
  }

  .show img{
    width: 100%;
    height: 93%;
    border-radius: 5%;
  }

  .home{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .pageTitle{
    color: #292929;
    font-family: 'Crete Round', serif;
    font-size: 3em;
  }

  /* Page change buttons  */

  .changepage{
    display: -ms-flexbox;
    display: flex;
    width: 84%;
    -ms-flex-align:center;
        align-items:center; 
    -ms-flex-direction: row; 
        flex-direction: row;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    padding-right: 8%;
    padding-left: 8%;
    padding-bottom: 13%;
  }

  .pagebtn{
    color: white;
    font-size: 1em;
    height: 40px;
    width: 30%;
    background-color: #08acd5;
    text-decoration: none;
    border: 1px solid #08acd5;
    border-radius: 5%;
  }

  .currentpage{
    font-family: 'Crete Round', serif;
    font-size: 2em;
    color: #292929;
  }

  footer{
    position: fixed;
    background-color: #08acd5;
    color: white;
    right: 0;
    left: 0;
    bottom: 0;
    padding: 3%;
    text-align: center;
    margin-top: 5%;
    font-family: 'Crete Round', serif;
  }

  /* login and register forms  */

  .formcontainer{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    padding-bottom: 5%;
  }

  .formcontainer form{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: justify;
        justify-content: space-between;
    width: 55%;
    padding: 5% 10% 0% 10%;
    border-radius: 7%;
  }
  .containerform{
    padding: 5%; 
    margin-bottom: 20%;
  }

  .containerform-reg{
    padding: 5%; 
    margin-bottom: 5%;
  }

  .loginbtn{
    color: white;
    font-size: 1.3em;
    height: 40px;
    width: 100%;
    background-color: #292929;
    text-decoration: none;
    border: 2px solid #292929;
    border-radius: 4%;
  }

  .search-submit{
    color: white;
    font-size: .65em;
    height: 10%;
    width: 50%;
    background-color: #292929;
    text-decoration: none;
    border: 2px solid #292929;
    border-radius: 4%;
  }


  .search-bar{
    border: 1px solid #292929;
    padding-top:5%;
    margin-top: 7%;
  }

  
 
@media screen and (min-width: 768px) {

  .search-submit{
    width: 110%;
    font-size: .55em;
  }

  .search{
    display: -ms-flexbox;
    display: flex;
   -ms-flex-direction: row;
       flex-direction: row;
   -ms-flex-align: center;
       align-items: center
  }

  .mobile-nav {
    -ms-flex-pack: justify;
        justify-content: space-between;
  }

  .header {
    -ms-flex-direction: row;
        flex-direction: row;
    display: -ms-flexbox;
    display: flex; 
  }

  .navBar {
    display: -ms-flexbox;
    display: flex;
    visibility: visible;
    -ms-flex-direction: row;
        flex-direction: row;
    width: 600px;
    -ms-flex-pack: end;
        justify-content: flex-end; 
  }
  .navBar li{
    margin-top: 0%;
    width: 100px;
  }

  .wide-load{
    width: 100px;
  }

  .nav {
    margin-top: 0%;
    right: 0%;
  }

  .navBar a{
    -ms-flex-direction: row;
        flex-direction: row;
    color: #292929;
    font-size: 1.25em;
    right: 0%;
    padding: 3%;
  }

  .navBar a:hover {
  color: #ee1800;
}

  .slideBar {
    visibility: hidden;
    padding-right: 0%;
    padding-left: 0%;
    padding-top: 0%;
  }

  .slide {
    position: relative;
    -ms-flex-pack: start;
        justify-content: flex-start;
    width: 50%;
    visibility: hidden;
    right: 16%;
    overflow-x: visible;
  }

  .mobile-menu {
    display: none;
  }

  .show{
    display: inline-block;
    width: 20%;
    height: 330px;
    margin: 0 2.5%;
    margin-bottom: 7%;
  }

  .show img{
    width: 250px;
    height: 370px;
  }
  .show img:hover{
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
  }
  
  .pagebtn:hover{
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
  }

  .changepage{
    width: 40%;
    padding-right: 30%;
    padding-left: 30%;
    padding-bottom: 6%;
  }

  .formcontainer form{
    width: 20%;
  }

  .containerform:hover{
    border: 1px solid #ee1800;
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
  }

  .containerform-reg:hover{
    border: 1px solid #ee1800;
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
  }

  .loginbtn:hover{
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
  }
  
  footer{
    margin-top: 3%;
    padding: 1%;
  }

}



